.navbar{
    display: flex;
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 60px;
    justify-content: space-between;
    background: transparent;
    top: 0;
    
    /* background-color: #283747; */
}
.navbar a{
    text-decoration: none;
    font-size: 25px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: whitesmoke;
margin-bottom: -15px;
  padding: 10px;
    height: 60px;
    
}
.navbar div{
    display: flex;
    gap: 30px;
    height: 60px;
    text-align: center;
    align-items: center;
}
.navbar div a:hover{background-color: whitesmoke;color: #283747;}

.a{
    height: 750px;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 1350px;
    background-image: url(https://images.pexels.com/photos/163143/sackcloth-sackcloth-textured-laptop-ipad-163143.jpeg?auto=compress&cs=tinysrgb&w=600);}